import React from "react";
import '../scss/react-d3-radar.scss'
import * as d3 from 'd3'
import {Tooltip} from "react-bootstrap";
import uuidv from "uuid/v4";
class RadarDiagramD3 extends React.Component {
    constructor(props) {
        super(props)
        this.id = '_' + uuidv().replace(/-/g, '')
        this.getId = this.getId.bind(this)
        this.setId = this.setId.bind(this)
    }
    getId(id) {
        return '#' + this.id + '-' + id;
    }
    setId(id) {
        return this.id + '-' + id;
    }
    componentDidMount() {
        let showTooltip = (mouse) => {
            console.log(mouse)//mouse place
        }
        let angleToCoordinate = (angle, value) => {
            let x = Math.cos(angle) * radialScale(value);
            let y = Math.sin(angle) * radialScale(value)
            return {"x": 150+x, "y": 150+y};
        }
        let getPathCoordinates = (data_point,features) => {
            let coordinates = [];
            for (var i = 0; i < features.length; i++){
                let ft_name = features[i];
                let angle = (Math.PI / 2) + (2 * Math.PI * i / features.length);
                coordinates.push(angleToCoordinate(angle, data_point[ft_name]));
            }
            return coordinates;
        }
        let data = [
            {
                A:4,
                B:3,
                C:5,
                D:1,
                E:5,
                F:3,
                G:4,
                H:3,
                I:5,
                J:1
            }
        ]
        let features = []
        for (var key in data[0]){
            features.push(key)
        }
        let colors = ["darkorange"];
        let radialScale = d3.scaleLinear()
            .domain([0,10])
            .range([0,200]);
        let ticks = [1,2,3,4,5];//draw circles
        let line = d3.line()
            .curve(d3.curveBasisClosed)
            .x(function(d) { return d.x; })
            .y(function(d) { return d.y; });
        let svg = d3.select(this.getId("svg"))
        ticks.forEach(t =>
            svg.append("circle")
                .attr("cx", 150)//center
                .attr("cy", 150)//center
                .attr("r", radialScale(t))
                .attr("fill", function () {
                    if (t % 2 === 0) {
                        return "#2D3642"
                    } else {
                        return "#424B58"
                    }
                })
                .attr("opacity",function () {
                    return 0.2
                })
        );
        for (var i = 0; i < data.length; i ++){
            let d = data[i];
            let color = colors[i];
            let coordinates = getPathCoordinates(d,features);
            //draw the path element
            svg.append("path")
                .datum(coordinates)
                .attr("d",line)
                .attr("stroke-width", 3)
                .attr("stroke", color)
                .attr("fill", color)
                .attr("stroke-opacity", 1)
                .attr("opacity", 0.2)
                .on("mouseover",function (d, i, nodes) {
                    showTooltip(d3.mouse(this))
                    d3.select(nodes[i])
                        .attr("opacity",0.4)
                })
                .on("mouseout",function (d, i, nodes) {
                    d3.select(nodes[i])
                        .attr("opacity",0.2)
                })
        }
    }
    render() {
        return (
            <div id={"svg-container"}>
                <Tooltip id={this.setId("mytooltip")} style={{opacity:0}} >
                    <div style={{color:"white"}} id={this.setId("tooltip-text")}>
                    </div>
                </Tooltip>
                <svg id={this.setId("svg")} width={300} height={300}>
                </svg>
            </div>
        )
    }
}
export default RadarDiagramD3;
